import React, { useState } from 'react'
import { Divider, Grid, TextField, Typography } from '@mui/material'
import PaymentIcon from '@mui/icons-material/Payment';
import SearchIcon from '@mui/icons-material/Search';
import { CircularButton } from '../../components/buttons/CircularButton';
import { Loading } from '../../components/Loadings/Loading';
import ErrorAlert from '../../components/Alerts/ErrorAlert';
import { useDispatch, useSelector } from 'react-redux';
import { uiErrorMessage } from '../../redux/actions/ui.actions';
import { GetAllPagos, PagosInit } from '../../redux/actions/pagos.actions';
import { DataGrid } from '@mui/x-data-grid';
import { CustomToolbar } from '../../components/TableGrid/CustomToolbar';
import { CustomPagination } from '../../components/TableGrid/CustomPagination';
import { PagosColumns } from '../../data/Columns';
import { useEffect } from 'react';

const ListaPagos = () => {
    const dispatch = useDispatch();
    const [fecha_ini, setFecha_ini] = useState('');
    const [fecha_fin, setFecha_fin] = useState('');
    const { list_pagos } = useSelector(state => state.pagos);

    useEffect(() => {
        dispatch(PagosInit());
    }, [])

    const handleChangeF1 = (e) => {
        e.preventDefault();
        setFecha_ini(e.target.value);
    };
    const handleChangeF2 = (e) => {
        e.preventDefault();
        setFecha_fin(e.target.value);
    };

    const handleSearch = () => {
        if (!fecha_ini) return dispatch(uiErrorMessage('Porfavor Seleccione un fecha de inicio'));
        if (!fecha_fin) return dispatch(uiErrorMessage('Porfavor Seleccione un fecha final'));
        dispatch(GetAllPagos(fecha_ini, fecha_fin));
    };
    return (
        <>
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Typography variant="h6" gutterBottom>
                    Lista de pagos
                </Typography>
                <PaymentIcon />
            </div>
            <Divider />
            <Grid container spacing={1} sx={{ mt: 2 }} style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <Grid item xs={12} md={4}>
                    <TextField
                        type="date"
                        label="Fecha Inicio"
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                        name="fecha_ini"
                        value={fecha_ini}
                        onChange={handleChangeF1}
                    />
                </Grid>
                <Grid item xs={12} md={4}>
                    <TextField
                        fullWidth
                        type="date"
                        label="Fecha Fin"
                        InputLabelProps={{ shrink: true }}
                        name="fecha_fin"
                        value={fecha_fin}
                        onChange={handleChangeF2}
                    />
                </Grid>
                <Grid item xs={12} md={4} style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }} >
                    <CircularButton type="button" text="Buscar" Icon={SearchIcon} handleClick={handleSearch} />
                </Grid>
                <Grid item xs={12} md={12}>
                    {
                        list_pagos && list_pagos?.length > 0 && (
                            <div style={{ marginTop: 20, height: '30rem', width: 'auto', overflow: 'auto', cursor: 'pointer' }}>
                                <DataGrid
                                    initialState={{
                                        sorting: {
                                            sortModel: [{ field: 'fecha_pago', sort: 'asc' }]
                                        },
                                    }}
                                    getRowId={(item) => item._id}
                                    density="compact"
                                    rows={list_pagos}
                                    columns={PagosColumns}
                                    pageSize={10}
                                    rowsPerPageOptions={[10]}
                                    loading={false}
                                    // onRowClick={selectItem}
                                    // onCellKeyDown={(params, event) => { handleEnter(params, event) }}
                                    editMode='row'
                                    components={{
                                        Toolbar: CustomToolbar,
                                        Pagination: CustomPagination,
                                    }}
                                    // componentsProps={{
                                    //     toolbar: { onClick: handleAdd }
                                    // }}
                                    // checkboxSelection
                                    experimentalFeatures={{ newEditingApi: true }}

                                />
                                <Loading />
                                <ErrorAlert />
                            </div>
                        )
                    }
                </Grid>
            </Grid>
            <Loading />
            <ErrorAlert />
        </>
    )
}

export default ListaPagos