import { fetchToken } from "../../helpers/fetch";
import { PagosConstants } from "../constants/pagos.constants";
import { uiConstants } from "../constants/ui.constanst";


export const PagosInit = ()=>{
    return (dispatch)=>{
        dispatch({type:PagosConstants.Initial});
    }
}

export const GetAllPagos=(fecha_ini,fecha_fin)=>{
    return async (dispatch)=>{
        try {
            dispatch({ type: uiConstants.uiOpenLoading });
            const resp = await fetchToken('prestamos/getAllPagos',{fecha_ini,fecha_fin},"POST");
            const body = await resp.json();
            if (body.ok) {
                dispatch({ type: PagosConstants.getAll ,payload:body.response});
                dispatch({ type: uiConstants.uiErrorMessage, payload: null });
            }
            else {
                dispatch({ type: uiConstants.uiErrorMessage, payload: body.msg });
            }
            dispatch({ type: uiConstants.uiCloseLoading });
        } catch (error) {
            console.error(error);
            dispatch({ type: uiConstants.uiCloseLoading });
            dispatch({ type: uiConstants.uiErrorMessage, payload: JSON.stringify(error) })
        }
    }
}